import React from 'react';

import Card from '../components/Card';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';


import storm from '../assets/images/storm2.png';
import ecodatalab from '../assets/images/ecodatalab.png';
import textsummarizer from '../assets/images/textsummarizer.png';
import click from '../assets/images/click.png';

class Carousel extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      items: [
        {
          id: 0,
          title: 'Storm',
          subTitle: 'Multipurpose Messenging Bot',
          imgSrc: storm,
          link: 'https://github.com/bbenner7635/Storm',
          selected: false
        },
        {
          id: 1,
          title: 'EcoDataLab',
          subTitle: 'Front-end team at U.C. Berkeley',
          imgSrc: ecodatalab,
          link: 'https://ecodatalab2.web.app/',
          selected: false
        },
        {
          id: 2,
          title: 'Text Summarizer',
          subTitle: 'A text summarizer using sentence scoring',
          imgSrc: textsummarizer,
          link: 'https://github.com/bbenner7635/Text-Summarizer',
          selected: false
        },
        {
          id: 3,
          title: 'Click',
          subTitle: 'A static social media webpage',
          imgSrc: click,
          link: 'http://clicksite.gq/',
          selected: false
        },
      ]
    }
  }

  handleCardClick = (id, card) => {
    let items = [...this.state.items];

    items[id].selected = items[id].selected ? false : true;

    items.forEach(item => {
      if(item.id !== id) {
        item.selected = false;
      }
    });

    this.setState({
      items
    });
  }

  makeItems = (items) => {
    return items.map(item => {
      return <Card item={item} click={(e => this.handleCardClick(item.id, e))} key={item.id} />
    });
  }

  render() {
    return (
      <Container fluid={true}>
        <Row className="justify-content-around">
          {this.makeItems(this.state.items)}
        </Row>
      </Container>
    );
  }

}

export default Carousel;
